header {
  font-family: "Barlov", sans-serif !important;
}
header .container {
  height: 70px;
}
header .container .row {
  margin: 10px 15px;
  max-width: 100%;
  height: 70px;
}
header .lang-switcher {
  display: inline-block;
  width: fit-content;
}
header .lang-switcher img {
  width: 28px;
  vertical-align: middle;
}
header .lang-switcher a:hover {
  background: transparent;
}
header ul {
  width: 65%;
}
header ul li a {
  color: white !important;
}
header .menu-large {
  width: 65vw;
  text-align: right;
}
header .contact {
  float: right;
  position: static !important;
}
header .contact p {
  color: white !important;
}
header .contact i {
  color: #ee3042 !important;
  opacity: 1 !important;
}
header .contact a {
  opacity: 1 !important;
}
header .contact .phone {
  font-size: 15px;
}
header .menu-mobile-overlay {
  background: rgba(0, 0, 0, 0.85);
  position: absolute;
  height: auto;
  padding: 5vh;
  width: 100vw;
  top: 0;
  left: 0;
  display: none;
}
header .menu-mobile-overlay .menu-mobile {
  display: none;
  float: none;
  width: 100%;
}
header .menu-mobile-overlay .menu-mobile li {
  font-size: 12px;
  display: block;
  margin-top: 4vh;
}
header .menu-mobile-overlay .menu-mobile li a {
  background: transparent;
  border-bottom: 1px solid white;
}
header .menu-mobile-overlay .menu-mobile .close-mobile-menu {
  background: transparent;
  font-size: 30px;
}
header .menu-mobile-overlay .menu-mobile .close-mobile-menu a {
  background: transparent;
  border-bottom: none;
}

.nextgen_pro_grid_album .image_container {
  width: 23% !important;
}

.page-id-47027 .galleria-info-description {
  text-align: center !important;
  margin: 20px auto !important;
  color: white !important;
  font-weight: bold !important;
  font-size: 2em !important;
  width: fit-content;
  display: block !important;
}

.reportaze .galleria-info-description {
  margin: 20px 15px !important;
  color: white !important;
  font-weight: bold !important;
  font-size: 1em !important;
}

.images-search {
  width: 30%;
  margin: 5% auto;
  margin-bottom: 2em;
}
.images-search input {
  width: 100%;
  padding: 10px;
  border-radius: 90px;
  border: 2px solid black;
}
.images-search input::placeholder {
  color: black;
}
.images-search input:focus-visible {
  outline: none !important;
}
.images-search__not-found {
  color: #ee3042;
  display: none;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: underline;
}
.images-search__results-found {
  display: none;
  color: white;
  text-align: center;
  text-transform: uppercase;
}

/*------------------------------- MOBILE -------------------------------*/
@media (max-width: 500px) {
  .nextgen_pro_grid_album .image_container {
    width: 90% !important;
  }

  .images-search {
    width: 50%;
  }
  .images-search input {
    border: 1px solid black;
  }

  .page-id-47027 .galleria-info-description {
    font-size: 1em !important;
    text-align: left !important;
    margin-left: 4% !important;
  }

  .reportaze .galleria-info-description {
    font-size: 0.8em !important;
  }
}
@media (min-width: 500) and (max-width: 1366px) {
  .nextgen_pro_grid_album .image_container {
    width: 30% !important;
  }
}
/*------------------------------------ SLICK  ------------------------------*/
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/*------------------------------------ SLICK END ------------------------------*/
