header ul.side-nav li.menu-item-has-children span:hover + a {
  color: #fff;
}

@media only screen and (max-width: 992px) {
  header ul li.menu-item-has-children span {
    width: 100%;
    height: 64px;
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
    z-index: 10;
  }

  header ul li.menu-item-has-children .sub-menu {
    position: static;
    z-index: 1;
    height: 0;
    width: 100%;
    overflow: hidden;
  }

  header ul li.menu-item-has-children.active .sub-menu {
    visibility: visible;
    height: auto;
  }

  header ul li.menu-item-has-children .sub-menu a {
    font-size: 10px;
  }

  header ul {
    float: none;
  }
}
